import { useHandlerMutation } from "Helpers/Hooks";
import { useForm }            from "react-hook-form";
import { useHistory }         from "react-router-dom";
import AddAppraisal           from "./AddAppraisal";

const AddAppraisalContainer = () => {

	const history = useHistory();

	const [ createAppraisal, { isLoading } ] = useHandlerMutation({
		toastName       : "apprasialCRUD",
		successCallback : () => history.push("/app/appraise/records"),
	});

	const hookForm = useForm({
		defaultValues : {
			general_information : {
				state        : null,
				municipality : null,
				folio        : "",
				colony       : "",
				street       : "",
				num_ext      : "",
				num_int      : "",
				zip_code     : "",
				section      : "",
				sub_section  : "",
				lot          : "",
				condominium  : "",
				building     : "",
				level        : null,
				streets      : ["", ""],
			},

			applicant : {
				paternal_last_name : "",
				maternal_last_name : "",
				name               : "",
				person_type        : null,
				mancomunated       : "",
				rfc                : "",
				curp               : "",
				tel                : "",
				phone              : "",
				zip_code           : "",
				municipality       : null,
				colony             : "",
				state              : null,
				street             : "",
				num_ext            : "",
				num_int            : "",
				block              : "",
				lot                : "",
				security_code      : "",
			},

			controller : null,
			perit      : null,
		},
	});

	const { handleSubmit } = hookForm;

	const onSubmit = handleSubmit(( data ) => {
		const dataCopy = { ...data };

		delete dataCopy["controller"];
		delete dataCopy["perit"];

		const id_perit      = data.perit.value;
		const id_controller = data.controller.value;

		const dataToSubmit = {
			...dataCopy,
			proficient : {
				id_perit,
				id_controller,
			},
		};

		dataToSubmit.applicant.colony = dataToSubmit.applicant.colony.value;

		createAppraisal({
			module : "appraisals",
			data   : dataToSubmit,
			tags   : ["appraisals"],
		});
	});

	return (
		<AddAppraisal
			delegations={{
				onSubmit,
				hookForm,
				isLoading,
			}}
		/>
	);
};

export default AddAppraisalContainer;
