import { Button }   from "reactstrap";
import { useWatch } from "react-hook-form";

import { FormRHF } from "Components/Global";

import {
	ExpertForm,
	ApplicantForm,
	GeneraPropertylInfo,
} from "Components/Appraisal";

const AddAppraisal = ({
	delegations : {
		onSubmit,
		hookForm,
		isLoading,
	},
}) => {

	const { isDirty, control } = hookForm;

	const municipalityWatcher = useWatch({ name : "general_information.municipality", control  });

	return (
		<FormRHF hookFormMethods={hookForm} onSubmit={onSubmit}>
			<GeneraPropertylInfo />
			{
				municipalityWatcher?.value !== undefined &&
				<>
					<ApplicantForm />
					<ExpertForm />
					<div className="d-flex justify-content-center my-1">
						<Button disabled={isDirty || isLoading} color="primary">Guardar</Button>
					</div>
				</>
			}
		</FormRHF>
	);
};

export default AddAppraisal;
